import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
 {path: 'detailedstatement', loadChildren: () => import('../app/pages/common-component/detailed-statement/detailed-statement.module').then(m => m.DetailedStatementModule)},
 {path: 'myaccount', loadChildren: () => import('../app/pages/common-component/my-account/my-account.module').then(m => m.MyAccountModule)},
 { path: '', redirectTo: '/detailedstatement', pathMatch: 'full' }

  
];


@NgModule({
  declarations: [],
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
